import React, { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "../components/ui/Card";
import { Button } from "../components/ui/Button";
import { Input } from "../components/ui/Input";
import { Textarea } from "../components/ui/Textarea";
import { Label } from "../components/ui/Label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "../components/ui/Select";
import { MapPin, Phone, Mail, Clock, Heart, Send, Star } from "lucide-react";
import { motion } from "framer-motion";

export default function Contact() {
  const [formData, setFormData] = useState({ name: "", email: "", phone: "", service: "", message: "" });
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    // Simulate form submission
    await new Promise(resolve => setTimeout(resolve, 2000));
    alert("Vielen Dank fr Ihre Nachricht! Ich melde mich innerhalb von 24 Stunden bei Ihnen.");
    setFormData({ name: "", email: "", phone: "", service: "", message: "" });
    setIsSubmitting(false);
  };

  const contactInfo = [
    { icon: Phone, title: "Telefon", details: "+49 (0) 123 456 789", description: "Mo-Fr 9-18 Uhr, Sa 10-16 Uhr" },
    { icon: Mail, title: "E-Mail", details: "info@seelenwege.de", description: "Antwort innerhalb von 24h" },
    { icon: MapPin, title: "Praxis", details: "Musterstrae 123, 12345 Musterstadt", description: "Zentral gelegen, gut erreichbar" },
    { icon: Clock, title: "ffnungszeiten", details: "Mo-Fr 9:00 - 18:00 Uhr", description: "Termine nach Vereinbarung" }
  ];

  return (
    <div className="container mx-auto px-4 py-12">
      <motion.h1 
        initial={{ opacity: 0, y: 20 }} 
        animate={{ opacity: 1, y: 0 }} 
        className="text-4xl font-bold text-center mb-8"
      >
        Kontakt
      </motion.h1>
      <p className="text-lg text-center mb-12">
        Bereit fr den ersten Schritt auf Ihrer spirituellen Reise? Ich freue mich darauf, Sie kennenzulernen und Sie auf Ihrem Weg zu begleiten.
        Lassen Sie uns in einem unverbindlichen Gesprch herausfinden, wie ich Sie am besten untersttzen kann.
      </p>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <Card>
          <CardHeader>
            <CardTitle>Kontaktinformationen</CardTitle>
          </CardHeader>
          <CardContent className="grid gap-4">
            {contactInfo.map((info, index) => (
              <div key={index} className="flex items-start">
                <info.icon className="w-6 h-6 mr-4" />
                <div>
                  <h4 className="font-semibold">{info.title}</h4>
                  <p>{info.details}</p>
                  <p className="text-sm text-gray-500">{info.description}</p>
                </div>
              </div>
            ))}
          </CardContent>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Nachricht senden</CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Label htmlFor="name">Name</Label>
                <Input id="name" value={formData.name} onChange={(e) => handleInputChange('name', e.target.value)} required />
              </div>
              <div>
                <Label htmlFor="email">E-Mail</Label>
                <Input id="email" type="email" value={formData.email} onChange={(e) => handleInputChange('email', e.target.value)} required />
              </div>
              <div>
                <Label htmlFor="phone">Telefon</Label>
                <Input id="phone" value={formData.phone} onChange={(e) => handleInputChange('phone', e.target.value)} />
              </div>
              <div>
                <Label htmlFor="service">Gewnschter Service</Label>
                <Select onValueChange={(value) => handleInputChange('service', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Whlen Sie einen Service" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="coaching">Coaching</SelectItem>
                    <SelectItem value="kurs">Kurs</SelectItem>
                    <SelectItem value="workshop">Workshop</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div>
                <Label htmlFor="message">Nachricht</Label>
                <Textarea id="message" value={formData.message} onChange={(e) => handleInputChange('message', e.target.value)} required />
              </div>
              <Button type="submit" disabled={isSubmitting}>
                <Send className="mr-2" /> {isSubmitting ? 'Wird gesendet...' : 'Absenden'}
              </Button>
            </form>
          </CardContent>
        </Card>
      </div>
      <div className="text-center mt-12">
        <p className="text-lg mb-4">
          ber 500 Menschen haben bereits ihren Weg mit meiner Untersttzung gefunden.
        </p>
        <div className="flex justify-center gap-4">
          <Badge><Star className="mr-2" /> Zertifizierte Lebensberaterin</Badge>
          <Badge><Heart className="mr-2" /> Kostenlose Erstberatung</Badge>
          <Badge><Clock className="mr-2" /> 100% Vertraulichkeit</Badge>
        </div>
      </div>
    </div>
  );
}
